      "headersStr": "Creating component classes Adding Components to an Entity Removing Components From an Entity Getting an Entities Component Getting All Components",
      "content": "# Components\n\nComponents are meant to be data bags and nothing more. All logic should be placed in (EntitySystems). So it is recommended to declare components like plain c-structs. Adding getters and setters is acceptable though.\n\n\n# Creating component classes\n\nTo define a new component, you only need to extend the Component class.\n\nIn the following example we define two components: PositionComponent and VelocityComponent.\n\nclass PositionComponent extends Component {\n\tx = 0;\n\ty = 0;\n}\nclass VelocityComponent extends Component {\n\tx = 0;\n\ty = 0;\n}\n\n\nThe collection of components an entity has will determine its behavior. Adding components to an entity is dead easy. However, mind that entities can not hold two components of the same class. So, when you add a component type twice, the second one will replace the first one.\n\n\n# Adding Components to an Entity\n\nYou can assign components easily using add:\n\nentity.add(new PositionComponent());\nentity.add(new VelocityComponent());\n\n\nAlternatively, you can assemble entities based on blueprints using a data-driven approach.\n\n\n# Removing Components From an Entity\n\nYou can remove components from entities any time (this might be delayed).\n\nentity.remove(PositionComponent); // remove just one specific component\nentity.removeAll(); // remove all components\n\n\n\n# Getting an Entities Component\n\nRetrieving components can be done as follows.\n\nconst position = entity.get(PositionComponent);\nconst velocity = entity.get(VelocityComponent);\n\n\nentity.get() will return null if the component does not exist in the entity. You can use entity.has() to check if the entity has a component of the specified class.\n\nYou can also get an entity using entity.require(). If the component does not exist, an exception will be thrown. You should use entity.require() only if you know that the component exist. For example if you are using an iterating EntitySystem, which already covers the existence with its Family.\n\nconst position = entity.require(PositionComponent);\n\n\n\n# Getting All Components\n\nYou can get all components at once by doing this.\n\nconst components = entity.getAll();\n\n\nYou can iterate over them either with a for of loop:\n\nfor (const component of components) {\n\t...\n}\n\n\nBut also using a classic loop:\n\nfor (let i = 0; i < components.length; ++i) {\n\tconst component = components[i];\n\t...\n}\n",
      "normalizedContent": "# components\n\ncomponents are meant to be data bags and nothing more. all logic should be placed in (entitysystems). so it is recommended to declare components like plain c-structs. adding getters and setters is acceptable though.\n\n\n# creating component classes\n\nto define a new component, you only need to extend the component class.\n\nin the following example we define two components: positioncomponent and velocitycomponent.\n\nclass positioncomponent extends component {\n\tx = 0;\n\ty = 0;\n}\nclass velocitycomponent extends component {\n\tx = 0;\n\ty = 0;\n}\n\n\nthe collection of components an entity has will determine its behavior. adding components to an entity is dead easy. however, mind that entities can not hold two components of the same class. so, when you add a component type twice, the second one will replace the first one.\n\n\n# adding components to an entity\n\nyou can assign components easily using add:\n\nentity.add(new positioncomponent());\nentity.add(new velocitycomponent());\n\n\nalternatively, you can assemble entities based on blueprints using a data-driven approach.\n\n\n# removing components from an entity\n\nyou can remove components from entities any time (this might be delayed).\n\nentity.remove(positioncomponent); // remove just one specific component\nentity.removeall(); // remove all components\n\n\n\n# getting an entities component\n\nretrieving components can be done as follows.\n\nconst position = entity.get(positioncomponent);\nconst velocity = entity.get(velocitycomponent);\n\n\nentity.get() will return null if the component does not exist in the entity. you can use entity.has() to check if the entity has a component of the specified class.\n\nyou can also get an entity using entity.require(). if the component does not exist, an exception will be thrown. you should use entity.require() only if you know that the component exist. for example if you are using an iterating entitysystem, which already covers the existence with its family.\n\nconst position = entity.require(positioncomponent);\n\n\n\n# getting all components\n\nyou can get all components at once by doing this.\n\nconst components = entity.getall();\n\n\nyou can iterate over them either with a for of loop:\n\nfor (const component of components) {\n\t...\n}\n\n\nbut also using a classic loop:\n\nfor (let i = 0; i < components.length; ++i) {\n\tconst component = components[i];\n\t...\n}\n",
      "charsets": {}
