      "headersStr": "Creating an Engine Dependency Injection Container",
      "content": "# Engine\n\nThe Engine class is the center of the framework. Typically, you would have only one instance per application.\n\nThe engine is responsible for setting up and handling entity & system managers, etc.\n\n\n# Creating an Engine\n\nYou can create an engine with the default constructor:\n\nconst engine = new Engine();\n\n\nYou will learn more about the operations you can do with Engine in the following tutorials.\n\n\n# Dependency Injection Container\n\nYou can use engine.getContainer() to get the instance of the dependency injection container for this engine. Currently typedi is being used.\n\nMake sure to check out the page about Dependency Injection.",
      "normalizedContent": "# engine\n\nthe engine class is the center of the framework. typically, you would have only one instance per application.\n\nthe engine is responsible for setting up and handling entity & system managers, etc.\n\n\n# creating an engine\n\nyou can create an engine with the default constructor:\n\nconst engine = new engine();\n\n\nyou will learn more about the operations you can do with engine in the following tutorials.\n\n\n# dependency injection container\n\nyou can use engine.getcontainer() to get the instance of the dependency injection container for this engine. currently typedi is being used.\n\nmake sure to check out the page about dependency injection.",
